: 12px 16px;\n    border: none;\n    border-radius: 8px;\n    font-size: 14px;\n    font-weight: 500;\n    cursor: pointer;\n    transition: all 0.2s;\n}\n\n.aurora-btn-primary {\n    background: linear-gradient(90deg, #22d3ee, #3b82f6);\n    color: #ffffff;\n}\n\n.aurora-btn-primary:hover {\n    opacity: 0.9;\n    transform: translateY(-1px);\n}\n\n.aurora-btn-primary:disabled {\n    opacity: 0.5;\n    cursor: not-allowed;\n    transform: none;\n}\n\n.aurora-btn-link {\n    background: none;\n    border: none;\n    color: #22d3ee;\n    font-size: 12px;\n    cursor: pointer;\n    padding: 0;\n}\n\n.aurora-btn-link:hover {\n    text-decoration: underline;\n}\n\n/* User Info */\n.aurora-user-info {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 8px 12px;\n    background: rgba(255, 255, 255, 0.05);\n    border-radius: 8px;\n    margin-bottom: 16px;\n    font-size: 13px;\n}\n\n/* Panel (structure comme SaaS AIPanel) */\n.aurora-panel {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n    max-height: calc(100vh - 200px);\n    overflow-y: auto;\n}\n\n.aurora-panel-section {\n    background: rgba(39, 39, 42, 0.5);\n    border: 1px solid rgba(63, 63, 70, 0.5);\n    border-radius: 8px;\n    padding: 12px;\n}\n\n.aurora-panel-section.aurora-panel-formatting {\n    background: linear-gradient(135deg, rgba(6, 182, 212, 0.1), rgba(59, 130, 246, 0.1));\n    border-color: rgba(6, 182, 212, 0.2);\n}\n\n.aurora-panel-section h3 {\n    font-size: 13px;\n    font-weight: 500;\n    margin-bottom: 10px;\n    display: flex;\n    align-items: center;\n    gap: 6px;\n}\n\n.section-icon {\n    font-size: 14px;\n}\n\n.aurora-panel-section .aurora-action-btn {\n    display: block;\n    width: 100%;\n    text-align: left;\n    padding: 10px 12px;\n    margin-bottom: 6px;\n}\n\n.aurora-panel-section .aurora-action-btn:last-of-type {\n    margin-bottom: 0;\n}\n\n.aurora-select {\n    width: 100%;\n    padding: 8px 12px;\n    marg